@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
.content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.search {
  width: 350px;
  border-radius: 5px;
  padding: 10px;
  margin: 50px 50px 0px 0px;
  background-color: white;
  box-shadow: 0 0 8px #d9d9d9;
  border: none;
}
.searchButton {
  padding: 10px 15px 10px 15px;
  background: black;
  color: white;
  border: none;
  border-radius: 2px;
}
.searchKeyword {
  font-size: 25px;
}
.total {
  opacity: 50%;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 10px 0px;
  flex-wrap: wrap;
}

img {
  margin-right: 20px;
  width: 250px;
  height: 150px;
  border-radius: 4px;
}

* {
  font-family: "Roboto Condensed", sans-serif;
}

.main {
  margin: 50px 100px;
}
label {
  margin-left: 300px;
}

.loadmoreButton {
  /* visibility: hidden; */
  padding: 10px 15px 10px 15px;
  background: blueviolet;
  color: white;
  border: none;
  border-radius: 2px;
  margin-left: 50%;
  margin-top: 10px;
}

